import { Component, OnInit } from '@angular/core';
import { User } from 'app/model/user';
import { LoginService } from 'app/services/login.service';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
    selector: 'app-signup',
    templateUrl: './signup.component.html',
    styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {
    test : Date = new Date();
    
    public user: User = {
        
        email: null,
        pass: null,
        nom: null,
        prenom: null
    }
    
    constructor(private loginService: LoginService, private router: Router , private flashMessage : FlashMessagesService) { }
    
    ngOnInit() {}
    connect = () => {
        this.loginService.getUser(this.user).subscribe((authResult: User[]) => {
            if (authResult.length) { 
    
                this.router.navigateByUrl('/accueil') 
            }
            else{
                this.showFlash();
            }
        })
    }
    showFlash() {
        this.flashMessage.show('Mot de passe ou email incorrect', { cssClass: 'alert-error', timeout: 8000 });
    }
}
