import { Component, OnInit } from '@angular/core';
import { User } from 'app/model/user';
import { RegisterService } from 'app/services/register.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
  public user: User = {
    
    email: null,
    pass: null,
    nom: null,
    prenom: null
  }
  constructor(private registerService:RegisterService,private flashMessage: FlashMessagesService, private router: Router) { }

  ngOnInit() {
  }
  saveUser = () => {
    console.log(this.user.email)
    this.registerService.registerUser(this.user).subscribe(suc => { this.showFlash()});
    this.router.navigateByUrl('/signup')
  
  }
  showFlash() {
    this.flashMessage.show('Vous etes bien enregister', { cssClass: 'alert-success', timeout: 5000 });
}
}
