import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http'
import { User } from '../model/user'
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators/'
@Injectable()

export class RegisterService {

  constructor(private http: HttpClient) { }
  // app http pour persister dans la BD un user une service http
  registerUser = (auth : User): Observable<User>=> {
    console.log("dedans le service"+auth.email)
     return this.http.post<User>("http://localhost:3000/authentication",auth).pipe(
      catchError((e: HttpErrorResponse)  =>  throwError(e))
     )
  }
}
