import { NgModule } from '@angular/core';
import { CommonModule, } from '@angular/common';
import { BrowserModule  } from '@angular/platform-browser';
import { Routes, RouterModule } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { ProfileComponent } from './profile/profile.component';
import { SignupComponent } from './signup/signup.component';
import {RegisterComponent} from './register/register.component'
import { LandingComponent } from './landing/landing.component';
import { NucleoiconsComponent } from './components/nucleoicons/nucleoicons.component';
import { AccueilComponent } from './accueil/accueil.component';
import { Tours360Component } from './tours360/tours360.component';
import { AboutComponent } from './about/about.component';
import { ToursvrComponent } from './toursvr/toursvr.component';
import { ContacteznousComponent } from './contacteznous/contacteznous.component';



// TODO : Add your component (pages VR tours ...) that's allow to call them in html files with the key "path"
const routes: Routes =[
    { path: 'home',             component: HomeComponent },
    { path: 'user-profile',     component: ProfileComponent },
    { path: 'signup',           component: SignupComponent },
    { path: 'register',           component: RegisterComponent },
    { path: 'landing',          component: LandingComponent },
    { path: 'nucleoicons',      component: NucleoiconsComponent },
    { path: 'accueil',      component: AccueilComponent },
    { path: 'tours360', component: Tours360Component},
    {path:'about', component: AboutComponent},
    {path:'toursvr',  component: ToursvrComponent},
    {path:'contacteznous', component: ContacteznousComponent},
    { path: '', redirectTo: 'home', pathMatch: 'full' }
];

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    RouterModule.forRoot(routes)
  ],
  exports: [
  ],
})
export class AppRoutingModule { }
